       <!--:key=\"item.value\"-->\r\n              <!--:label=\"item.label\"-->\r\n              <!--:value=\"item.value\">-->\r\n              <!--</el-option>-->\r\n              <!--</el-select>-->\r\n              <!--</el-form-item>-->\r\n              <el-form-item>\r\n                <el-button type=\"primary\" @click=\"onSubmit\" size=\"small\" style=\"margin-top:0\">查询</el-button>\r\n              </el-form-item>\r\n            </el-form>\r\n            <el-button\r\n              class=\"right mb-10\"\r\n              type=\"primary\"\r\n              size=\"small\"\r\n              icon=\"el-icon-document\"\r\n              @click=\"exportExcel\"\r\n            >导出excel</el-button>\r\n            <div\r\n              style=\"width:90%;margin-top:10px;\"\r\n              :class=\"{hidden:!showProgress, show:showProgress}\"\r\n            >\r\n              <el-progress\r\n                class=\"process-item\"\r\n                :color=\"customerColor\"\r\n                :stroke-width=\"10\"\r\n                :percentage=\"downloadPercentage\"\r\n                :status=\"downloadStatus\"\r\n              ></el-progress>\r\n            </div>\r\n          </div>\r\n          <template slot=\"columns\">\r\n            <el-table-column type=\"index\" label=\"序号\" width=\"60\"></el-table-column>\r\n            <el-table-column sortable prop=\"date\" label=\"日期\" width=\"120\"></el-table-column>\r\n            <el-table-column sortable prop=\"plat\" label=\"设备\" width=\"100\"></el-table-column>\r\n            <el-table-column sortable prop=\"ksjk_existing\" label=\"快速借款用户\" width=\"90\"></el-table-column>\r\n            <el-table-column sortable prop=\"uv\" label=\"uv\"></el-table-column>\r\n            <el-table-column sortable prop=\"phones_count\" label=\"手机号\"></el-table-column>\r\n            <el-table-column sortable prop=\"take_phone_new\" label=\"留手机号新用户\"></el-table-column>\r\n            <el-table-column sortable prop=\"register_count\" label=\"注册