n        beginTime: '',\r\n        endTime: ''\r\n      },\r\n      dataTable: {\r\n        pageSize: 50,\r\n        pageSizes: [10, 20, 50, 100, 1000, 5000, 10000, 20000],\r\n        url: '/web/v4/market/queryH5',\r\n        emptyText: '没有记录',\r\n        showSummary: true,\r\n        summaryMethod: this.getSummaries\r\n      },\r\n      hiddenColumns: [],\r\n      columnsOptions: [\r\n        {\r\n          value: 'date',\r\n          label: 'date'\r\n        },\r\n        {\r\n          value: 'plat',\r\n          label: 'plat'\r\n        },\r\n        {\r\n          value: 'ksjk_existing',\r\n          label: 'ksjk_existing'\r\n        },\r\n        {\r\n          value: 'uv',\r\n          label: 'uv'\r\n        },\r\n        {\r\n          value: 'phones_count',\r\n          label: 'phones_count'\r\n        },\r\n        {\r\n          value: 'register_count',\r\n          label: 'register_count'\r\n        },\r\n        {\r\n          value: 'activation_count',\r\n          label: 'activation_count'\r\n        },\r\n        {\r\n          value: 'jinjian',\r\n          label: 'jinjian'\r\n        },\r\n        {\r\n          value: 'approval',\r\n          label: 'approval'\r\n        },\r\n        {\r\n          value: 'emitCn',\r\n          label: 'emitCn'\r\n        },\r\n        {\r\n          value: 'emitMoney',\r\n          label: 'emitMoney'\r\n        },\r\n        {\r\n          value: 'act_reg_rate',\r\n          label: 'act_reg_rate'\r\n        },\r\n        {\r\n          value: 'jj_jh_rate',\r\n          label: 'jj_jh_rate'\r\n        },\r\n        {\r\n          value: 'review_jinj_rate',\r\n          label: 'review_jinj_rate'\r\n        },\r\n        {\r\n          value: 'jinjian_t7',\r\n          label: 'jinjian_t7'\r\n        },\r\n        {\r\n          value: 'approval_t7',\r\n          label: 'approval_t7'\r\n        },\r\n        {\r\n          value: 'emitCn_t7',\r\n          label: 'emitCn_t7'\r\n        },\r\n        {\r\n          value: 'emitMoney_t7',\r\n          label: 'emitMoney_t7'\r\n        },\r\n        {\r\n          value: 'review_jinj_rate',\r\n          label: 'review_jinj_rate'\r\n        }\r\n      ],\r\n      downloadPercentage: 0,\r\n      downloadStatus: \"warning\",\r\n      showProgress: false,\r\n      customerColor: '#409eff'\r\n    };\r\n  },\r\n  mounted() {\r\n    this.dataTable.queryParams = this.queryParams;\r\n    let date = new Date();\r\n    let beginDate = new Date(date.setDate(date.getDate() - 7));\r\n    this.initBeginTime = this.dealDate(beginDate);\r\n    this.initEndTime = this.dealDate(new Date());\r\n    this.dataTable.queryParams.beginTime = this.initBeginTime + ' 00:00:00';\r\n    this.dataTable.queryParams.endTime = this.initEndTime + ' 23:59:59';\r\n\r\n    var defaultPage = {\r\n      label: 'H5总计',\r\n      name: 'h5Statistics',\r\n      slotName: 'h5Statistics',\r\n      closable: false\r\n    };\r\n    this.$refs.tabPage.$emit('open-tab', defaultPage);\r\n  },\r\n  methods: {\r\n    loadLists() {\r\n      this.$refs.dataTable.$emit('load-data');\r\n    },\r\n    onSubmit: function() {\r\n      this.dataTable.queryParams = this.queryParams;\r\n      var tag = this.warnTime(\r\n        this.dataTable.queryParams.beginTime,\r\n        this.dataTable.queryParams.endTime,\r\n      );\r\n      if (tag) {\r\n        return;\r\n      }\r\n      // console.log(Object.prototype.toString.call(this.dataTable.queryParams.beginTime));\r\n      this.dataTable.queryParams.beginTime =\r\n        this.dealDate(this.dataTable.queryParams.beginTime) + ' 00:00:00';\r\n      this.dataTable.queryParams.endTime =\r\n        this.dealDate(this.dataTable.queryParams.endTime) + ' 23:59:59';\r\n      this.dataTable.url = '/web/v4/market/queryH5';\r\n      this.$refs.dataTable.$emit('load-data');\r\n    },\r\n    warnTime(startTime, endTime) {\r\n      if (!startTime) {\r\n        this.$notify({\r\n          message: '查询开始时间不能为空！',\r\n          type: 'warning'\r\n        });\r\n        return true;\r\n      }\r\n      if (!endTime) {\r\n        this.$notify({\r\n          message: '查询结束时间不能为空！',\r\n          type: 'warning'\r\n        });\r\n        return true;\r\n      }\r\n      let timeStart = new Date(startTime).getTime();\r\n      let timeEnd = new Date(endTime).getTime();\r\n      if (timeStart > timeEnd) {\r\n        this.$notify({\r\n          message: '查询开始时间不能大于结束时间',\r\n          type: 'warning'\r\n        });\r\n        return true;\r\n      }\r\n      return false;\r\n    },\r\n    dealTime(time) {\r\n      return formatDate(new Date(time), 'yyyy-MM-dd hh:mm:ss');\r\n    },\r\n    dealDate(date) {\r\n      return formatDate(new Date(date), 'yyyy-MM-dd');\r\n    },\r\n    formatterDate(row, column) {\r\n      return formatDate(row.date, 'yyyy-MM-dd');\r\n    },\r\n    getSummaries(param) {\r\n      const { columns, data } = param;\r\n      const sums = [];\r\n      columns.forEach((column, index) => {\r\n        if (index === 0) {\r\n          sums[index] = '总计';\r\n          return;\r\n        }\r\n        var values;\r\n        var itemProperty = column.property;\r\n        if (\r\n          itemProperty === 'act_reg_rate' ||\r\n          itemProperty === 'jj_jh_rate' ||\r\n          itemProperty === 'review_jinj_rate' ||\r\n          itemProperty === 'review_low_jinj_rate' ||\r\n          itemProperty === 'review_high_jinj_rate'\r\n        ) {\r\n          values = data.map(item => {\r\n            if (item[column.property] != null) {\r\n              return Number(item[column.property].replace('%', ''));\r\n            }\r\n          });\r\n        } else {\r\n          values = data.map(item => Number(item[column.property]));\r\n        }\r\n\r\n        if (!values.every(value => isNaN(value))) {\r\n          sums[index] = values.reduce((prev, curr) => {\r\n            const value = Number(curr);\r\n            if (!isNaN(value)) {\r\n              return prev + curr;\r\n            } else {\r\n              return prev;\r\n            }\r\n          }, 0);\r\n          if (\r\n            itemProperty === 'act_reg_rate' ||\r\n            itemProperty === 'jj_jh_rate' ||\r\n            itemProperty === 'review_jinj_rate' ||\r\n            itemProperty === 'review_low_jinj_rate' ||\r\n            itemProperty === 'review_high_jinj_rate'\r\n          ) {\r\n            console.log(sums[index], data, data.length);\r\n            sums[index] = (sums[index] / data.length).toFixed(2) + ' %';\r\n          }\r\n          // sums[index] += ' 元';\r\n        } else {\r\n          sums[index] = 'N/A';\r\n        }\r\n      });\r\n\r\n      return sums;\r\n    },\r\n\r\n    exportExcel() {\r\n      this.dataTable.queryParams = this.queryParams;\r\n      var tag = this.warnTime(\r\n        this.dataTable.queryParams.beginTime,\r\n        this.dataTable.queryParams.endTime,\r\n      );\r\n      if (tag) {\r\n        return;\r\n      }\r\n      // console.log(Object.prototype.toString.call(this.dataTable.queryParams.beginTime));\r\n      this.dataTable.queryParams.beginTime =\r\n        this.dealDate(this.dataTable.queryParams.beginTime) + ' 00:00:00';\r\n      this.dataTable.queryParams.endTime =\r\n        this.dealDate(this.dataTable.queryParams.endTime) + ' 23:59:59';\r\n      var exportParams = {};\r\n      Object.assign(exportParams, this.dataTable.queryParams);\r\n      exportParams.pageSize = this.$refs.dataTable.total;\r\n      exportParams.page = 1;\r\n      this.$refs.dataTable.$emit('export-data', exportParams);\r\n    },\r\n    exportDataStatus(statusInfo) {\r\n      if (statusInfo.status == 2) {\r\n        this.downloadPercentage = 100;\r\n        this.downloadStatus = 'success';\r\n      } else if (statusInfo.status == -1) {\r\n        this.downloadStatus = 'exception';\r\n      } else {\r\n        this.downloadPercentage = statusInfo.percent;\r\n        console.log(statusInfo.percent);\r\n        console.log(this.downloadPercentage);\r\n        this.showProgress = true;\r\n      }\r\n    },\r\n    exportDataSuccess() {\r\n      require.ensure([], () => {\r\n        const { export_json_to_excel } = require('vendor/Export2Excel');\r\n        const tHeader = [\r\n          '日期',\r\n          '设备',\r\n          '快速借款用户',\r\n          'uv',\r\n          '手机号',\r\n          '留手机号新用户',\r\n          '注册',\r\n          '激活',\r\n          '当日进件',\r\n          // '当日拒绝',\r\n          '当日审核',\r\n          '当日审核(额度<=1000)',\r\n          '当日审核(额度>1000)',\r\n          '当日放款',\r\n          '放款金额',\r\n          '激活/注册',\r\n          '进件/激活',\r\n          '审核/进件',\r\n          '审核/进件(额度<=1000)',\r\n          '审核/进件(额度>1000)',\r\n          'T7进件',\r\n          'T7审核',\r\n          'T7审核(额度<=1000)',\r\n          'T7审核(额度>1000)',\r\n          'T7放款',\r\n          'T7放款金额'\r\n        ];\r\n        const filterVal = [\r\n          'date',\r\n          'plat',\r\n          'ksjk_existing',\r\n          'uv',\r\n          'phones_count',\r\n          'take_phone_new',\r\n          'register_count',\r\n          'activation_count',\r\n          'jinjian',\r\n          // 'reject',\r\n          'approval',\r\n          'approval_low',\r\n          'approval_high',\r\n          'emitCn',\r\n          'emitMoney',\r\n          'act_reg_rate',\r\n          'jj_jh_rate',\r\n          'review_jinj_rate',\r\n          'review_low_jinj_rate',\r\n          'review_high_jinj_rate',\r\n          'jinjian_t7',\r\n          'approval_t7',\r\n          'approval_t7_low',\r\n          'approval_t7_high',\r\n          'emitCn_t7',\r\n          'emitMoney_t7'\r\n        ];\r\n        setTimeout(() => {\r\n          const list = this.$refs.dataTable.dataListA;\r\n          const data = this.formatJson(filterVal, list);\r\n          export_json_to_excel(tHeader, data, 'H5总计');\r\n        }, 500);\r\n      });\r\n    },\r\n    formatJson(filterVal, jsonData) {\r\n      return jsonData.map(v =>\r\n        filterVal.map(j => {\r\n          let returnValue;\r\n          if (j === 'operator') {\r\n            returnValue = v[j].name;\r\n          } else {\r\n            returnValue = v[j];\r\n          }\r\n          return returnValue;\r\n        }),\r\n      );\r\n    },\r\n\r\n    checkAllSelection(selectType) {\r\n      let len = this.$refs[selectType].optionsCount;\r\n      for (var i = 0; i < len; i++) {\r\n        (function(index, _this) {\r\n          setTimeout(() => {\r\n            _this.$refs[selectType].handleOptionSelect(\r\n              _this.$refs[selectType].options[index],\r\n            );\r\n          }, 0);\r\n        })(i, this);\r\n      }\r\n    },\r\n    handleCheckAllDeviceChange(event) {\r\n      this.checkAllSelection('deviceSelect');\r\n      this.deviceCheckAll = !this.deviceCheckAll;\r\n    },\r\n    setCheckAllStyle(selectedCount, selectType) {\r\n      let type = selectType + 'CheckAll';\r\n      let type_1 = selectType + 'Select';\r\n      if (this[type]) {\r\n        if (selectedCount.length < this.$refs[type_1].optionsCount) {\r\n          this[type] = !this[type];\r\n        }\r\n      } else {\r\n        if (selectedCount.length === this.$refs[type_1].optionsCount) {\r\n          this[type] = !this[type];\r\n        }\r\n      }\r\n    },\r\n    deviceChange(values) {\r\n      this.setCheckAllStyle(values, 'device');\r\n    }\r\n  }\r\n};\r\n</script>\r\n<style>\r\n.span-style {\r\n  display: inline-block;\r\n  margin-top: 8px;\r\n  font-size: 14px;\r\n}\r\n</style>\r\n"]}]);
